document.addEventListener('DOMContentLoaded', function() {
    iniciarApp();
});

function iniciarApp(){
    scrollNav();
    navegacionFija();
    mobileMenu();
    crearGaleria1();
    crearGaleria2();
    crearGaleria3();
    crearGaleria4();
    crearGaleria5();    
      
    check();
}



function scrollNav(){
    const enlaces = document.querySelector('.navegacion a');
    enlaces.addEventListener('click', function(e){
        e.preventDefault();
        const seccionScroll = e.target.attributes.href.value;
        const seccion = document.querySelector(seccionScroll);
        seccion.scrollIntoView({behavior: 'smooth'});
    });
}

function navegacionFija() {
    const barra = document.querySelector('.header');
    const sobreMi = document.querySelector('.sobre-mi');
    const body = document.querySelector('body');

    window.addEventListener('scroll', function () {
        console.log(sobreMi.getBoundingClientRect() );

        if(sobreMi.getBoundingClientRect().top < 0 ) {
            barra.classList.add('fijo');
            body.classList.add('body-scroll');
        } else {
            barra.classList.remove('fijo');
            body.classList.remove('body-scroll');
        }
    });
}


function mobileMenu(){
    const mobileMenu = document.querySelector('.mobile-menu');

    mobileMenu.addEventListener('click', navegacionResponsive);
}

function navegacionResponsive() {
    const navegacion = document.querySelector('.navegacion');
    navegacion.classList.toggle('mostrar');
}

const check = document.querySelector('.check');
check.addEventListener('click', idioma);

function idioma() {
    const id = check.checked;
    if (id===true) {
        location.href="es/index.html";
    }else {
        location.href="../index.html";
    }
}

function crearGaleria1() {
    const galeria = document.querySelector('.galeria-imagenes1');

    for(let i = 1; i <= 10; i++) {
        const imagen = document.createElement('picture');
        imagen.innerHTML = `
        <source srcset="../build/img/BlogCafeImagenesChicas/${i}.webp" type="image/webp">
        <img loading="lazy" width= "100" height="150" src="../build/img/BlogCafeImagenesChicas/${i}.jpg" alt="imagen galeria">
        `;
        imagen.onclick = function () {
            mostrarImagen1 (i);
        }

        galeria.appendChild(imagen);
    }
}
function mostrarImagen1(id) {
    const imagen = document.createElement('picture');
        imagen.innerHTML = `
        <source srcset="../build/img/BlogCafeImagenesGrandes/${id}.webp" type="image/webp">
        <img loading="lazy" width= "900"  height="500" src="../build/img/BlogCafeImagenesGrandes/${id}.jpg" alt="imagen galeria">
    `;
    //Crea el overlay con la imagen
   const overlay = document.createElement('DIV');
   overlay.appendChild(imagen);
   overlay.classList.add('overlay');
   overlay.onclick = function() {
        const body = document.querySelector('body');
        body.classList.remove('fijar-body');
        overlay.remove();
   }
   //Boton para cerrar el Modal
   const cerrarModal = document.createElement('P');
   cerrarModal.textContent = 'X';
   cerrarModal.classList.add('btn-cerrar');
   cerrarModal.onclick = function ( ) {
        const body = document.querySelector('body');
        body.classList.remove('fijar-body');
        overlay.remove();
   };
   overlay.appendChild(cerrarModal);
   //Lo añade al html
   const body = document.querySelector('body');
   body.appendChild(overlay);
   body.classList.add('fijar-body');
}   

